@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:wght@400;600;700&family=Martel:wght@400;600;700&display=swap);

.main-wrapper{margin:0 auto;font-family:'Crimson Text', 'Martel', serif}.main-header{margin:0 auto}

.Searchbar{top:0;left:0;position:-webkit-sticky;position:sticky;z-index:1100;display:flex;justify-content:center;align-items:center;min-height:64px;padding-right:24px;padding-left:24px;padding-top:12px;padding-bottom:12px;color:#fff;background-color:#3f51b5;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}.SearchForm{display:flex;align-items:center;width:100%;max-width:600px;background-color:#fff;border-radius:3px;overflow:hidden}.SearchForm-button{display:inline-block;width:48px;height:48px;border:0;background-image:url("https://image.flaticon.com/icons/svg/149/149852.svg");background-size:40%;background-repeat:no-repeat;background-position:center;opacity:0.6;transition:opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);cursor:pointer;outline:none}.SearchForm-button:hover{opacity:1}.SearchForm-button-label{position:absolute;width:1px;height:1px;padding:0;overflow:hidden;clip:rect(0, 0, 0, 0);white-space:nowrap;-webkit-clip-path:inset(50%);clip-path:inset(50%);border:0}.SearchForm-input{display:inline-block;width:100%;font:inherit;font-size:20px;border:none;outline:none;padding-left:4px;padding-right:4px}.SearchForm-input::-webkit-input-placeholder{font:inherit;font-size:18px}.SearchForm-input:-ms-input-placeholder{font:inherit;font-size:18px}.SearchForm-input::-ms-input-placeholder{font:inherit;font-size:18px}.SearchForm-input::placeholder{font:inherit;font-size:18px}

.repositories{width:100%}.repositories-list{display:flex;flex-direction:column;justify-content:center;flex-wrap:wrap;align-content:center;list-style:none;padding:0;margin-top:15px}

.star-wrapper{display:flex;align-items:center;justify-content:space-around}.repository{width:85%;padding:20px;border-radius:2px;margin-bottom:12px;box-shadow:13px 11px 14px -10px rgba(0,0,0,0.75)}.repository-header{display:flex;justify-content:space-between;font-weight:600;font-size:18px}.repository-header-link{position:relative;text-decoration:none;color:#3f51b5;transition:all ease-in-out 0.3s}.repository-header-link::after{content:'';position:absolute;left:0px;height:2px;bottom:-2px;width:0%;background-color:#3f51b5;transition:width 0.2s linear}.repository-header-link:hover::after{width:100%}.repository-star{width:30px;height:30px;display:inline-block;background-image:url("https://image.flaticon.com/icons/svg/2107/2107957.svg");background-size:100%;background-repeat:no-repeat;background-position:center}.repository-star-counter{margin:0;padding-left:8px}.repository-description{text-align:justify;line-height:1.4;font-size:16px}

.pagination{margin:0 auto}.pagination-list{list-style-type:none;justify-content:center;display:flex}.pagination-list-link{outline:none}.pagination-list-item{margin-bottom:15px;padding:10px;border:1px solid #3f51b5;border-radius:2px;color:#3f51b5;outline:none;cursor:pointer;border-right:none}.pagination-list-item:last-child{border-right:1px solid #3f51b5;margin-left:2px}.pagination-list-item:first-child{margin-right:2px;border-right:1px solid #3f51b5}.pagination-list-item_disabled{border:1px solid #9c9da3;color:#9c9da3}.pagination-list-item_active{background-color:#3f51b5;color:#fff}@media (max-width: 580px){.pagination-list{max-width:250px;margin:0 auto}.pagination-list-item{padding:8px}}

